.project {
    margin: 2%;
    border-radius: 10px;
    text-transform: lowercase;
    border: solid;
    padding: 3%;
    background-color: white;
    width: 50%;
}

a {
    font-size: 30px;
}

img {
    width: 100%;
    height: 100%;
    border-radius: 10px;
}

.project:hover {
    transform: scale(1.1);
    transition: 0.5s;
    cursor: pointer;
}

@media only screen and (max-width: 1400px) {

    .project {
        width: 95%;
    }
}